import { Component, OnInit, ViewChild } from '@angular/core';
import { ViewportScroller } from '@angular/common';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private viewportScroller: ViewportScroller) { }
  // constructor() { }

  @ViewChild("home", { static: false }) home;
  @ViewChild("about", { static: false }) about;
  @ViewChild("resume", { static: false }) resume;
  @ViewChild("portfolio", { static: false }) portfolio;
  @ViewChild("skills", { static: false }) skills;
  @ViewChild("contact", { static: false }) contact;



  navigateTo(element) {
    // console.log("element: " + this[element].nativeElement)
    this.viewportScroller.scrollToAnchor(element);
    // const test = document.querySelector(`#${element}`);
    // console.log(test)
    // if (test) test.scrollIntoView({ behavior: 'smooth', block: 'start' })
    // element.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
    // element.scrollIntoView({ behavior: "smooth" });
  }



  public onClick(elementId: string): void {
    this.viewportScroller.scrollToAnchor(elementId);
  }

  ngOnInit(): void {
  }
}
